<a-modal class="main" title="学生明细" destroyOnClose v-model:visible="props.visible" :width="800" @cancel="onCancel"><a-table class="table" rowKey="code" size="middle" :columns="columnStudent" :data-source="studentData" :pagination="false" :loading="loadingUserList"><template v-slot:studentEdit="{ text, record }"><div class="operation pointer" @click="editUser(record)"><b-icon type="iconbianji4"></b-icon></div></template><template v-slot:studentReset="{ text, record }"><div class="operation pointer" @click="resetUser(record)"><b-icon type="iconzhongzhi"></b-icon></div></template><template v-slot:studentDelete="{ text, record }"><div class="operation pointer" @click="delUser(record.code)"><b-icon type="iconshanchu"></b-icon></div></template></a-table><template #footer><div class="btn-group flex flex-row justify-between"><a-button key="create" type="primary" @click="changeBoolean('modalStudentAdd',true)">创建</a-button><div class="btn-right"><a-button key="back" type="primary" ghost @click="onCancel">取消</a-button><a-button key="submit" type="primary" @click="onCancel">确定</a-button></div></div></template><div class="page flex flex-row justify-end"><a-pagination :size="pageConfig.size" :current="pageConfig.current" :total="pageConfig.totalSize" :page-size="pageConfig.pageSize" :page-size-options="pageConfig.options" :show-quick-jumper="pageConfig.showJumper" :showSizeChanger="pageConfig.showSize" @change="getFetchData" @showSizeChange="onChangeSize"></a-pagination></div><the-edit-student key="modalStudentEdit" :isEdit="true" :visible="modalEditStudent" :classId="classId" @onSubmit="modalCallback('modalStudentEdit')" @onCancel="modalCallback('modalStudentEdit')"></the-edit-student><the-add-student key="modalStudentAdd" :isEdit="false" :visible="modalAddStudent" :classId="classId" @onSubmit="modalCallback('modalStudentAdd')" @onCancel="modalCallback('modalStudentAdd')"></the-add-student></a-modal>